import React, { useRef, useEffect, useState } from 'react';
import mapboxgl from 'mapbox-gl';
import Optionsfield from './components/Optionsfield';
import './Map.css';

import axios from 'axios';


import externallayers from './env/layers.json';
import externalStyling from './env/styling.json';
import accessKey from './env/key.json';

import Panel from './components/Panel';

mapboxgl.accessToken = accessKey.key;


const Map = () => {

  const baseMaps = [
    {
      name: 'Streets',
      url: 'mapbox://styles/mapbox/streets-v11'
    },
    {
      name: 'Satellite',
      url: 'mapbox://styles/mapbox/satellite-v9'
    },
    {
      name: 'Light',
      url: 'mapbox://styles/mapbox/light-v10'
    },
    {
      name: 'Dark',
      url: 'mapbox://styles/mapbox/dark-v10'
    }
  ];



  // const layers = Object.values(externallayers);

  const [layerlist, setLayerlist] = useState(null);
  const [isLoading, setLoading] = useState(true);

  const mapContainerRef = useRef(null);

  const [map, setMap] = useState(null);

  const [activeBaseMap, setBaseMap] = useState(baseMaps[0]);

  const [styling, setStyling] = useState(externalStyling);




  const repaintMap = () => {

    const test = [...layerlist].reverse();

    test.map(layer => {

      if (layer.checked) {
        map.addLayer({
          'id': layer.name,
          'type': layer.type,
          'source': layer.name,
          "source-layer": layer.column,
          "paint": styling[layer.name].paint


        });


      } else {
        if (map.getLayer(layer.name) !== undefined) {
          map.removeLayer(layer.name);
        }

      }
    });
  }



  // Initialize map when component mounts
  useEffect(() => {

    fetchData();

    const map = new mapboxgl.Map({
      container: mapContainerRef.current,
      style: activeBaseMap.url,
      center: [124, 8],
      zoom: 7,
      maxZoom: 15,
      minZoom: 7

    });

    map.on('load', () => { setMap(map) });
    map.addControl(new mapboxgl.NavigationControl());

    // Clean up on unmount
    return () => map.remove();
  }, []);




  useEffect(() => {
    if (map) {
      resetSources();
      repaintMap();
      setMap(map);

    }

  }, [layerlist]);


  useEffect(() => {
    if (map) {
      map.setStyle(activeBaseMap.url);
      map.on('style.load', () => {
        resetSources();
        repaintMap();
      })

    }

  }, [activeBaseMap]);


  const changeBaseMap = i => {
    setBaseMap(baseMaps[i]);

  };


  const fetchData = async () => {
    let response = await axios.get('data.json')
    let res = await response.data;
    setLayerlist(Object.values(res));
    setLoading(false);
  }


  const resetSources = () => {
    layerlist.map(layer => {

      if (map.getLayer(layer.name) !== undefined) {
        map.removeLayer(layer.name);

      }

      if (map.getSource(layer.name)) {
        map.removeSource(layer.name);
      }

      map.addSource(layer.name, {
        type: 'vector',
        url: 'mapbox://' + layer.url,
        minzoom: 2
      });


    });

  }

  if (isLoading) {
    return (
      <div>

        <div ref={mapContainerRef} className='map-container' />

      </div>
    );
  }


  return (
    <div>
      <div ref={mapContainerRef} className='map-container' />
      <Optionsfield
        basemaps={baseMaps}
        activeBaseMap={activeBaseMap}
        changeBaseMap={changeBaseMap}
      />
      <Panel
        maplayers={layerlist}
        setLayerlist={setLayerlist}
        styling={styling}
      />
    </div>
  );
};

export default Map;
